@import url(https://fonts.googleapis.com/css?family=Saira);
* {
  font-family: 'Saira', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-Detail {
  background-color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

a.no-underline {
  text-decoration: none;
}

input, textarea { 
  background-color:#FFFFFF;
  border:solid 1px #A9A9A9;
  font-size:15px;
  color:#000000;
  border-radius:5px;
  padding-top:5px;
  padding-bottom:5px;
  padding-left:6px;
  padding-right:6px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.brk-btn {
  position: relative;
  color: aqua;
  font-weight: 550;
  text-transform: uppercase;
  text-decoration: none;
  -webkit-border-image: -webkit-linear-gradient(295deg, #AAFFA9 0%, #06e5de 100%) 1 round;
          border-image: linear-gradient(155deg, #AAFFA9 0%, #06e5de 100%) 1 round;
  background-clip: text;
  -webkit-background-clip: text; }
  .brk-btn::before {
    content: "";
    display: block;
    position: absolute;
    width: 10%;
    background: #fff;
    height: 0.3em;
    right: 20%;
    top: -0.21em;
    -webkit-transform: skewX(-45deg);
            transform: skewX(-45deg);
    -webkit-transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1);
    transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1); }
  .brk-btn::after {
    content: "";
    display: block;
    position: absolute;
    width: 10%;
    background: #fff;
    height: 0.3em;
    left: 20%;
    bottom: -0.25em;
    -webkit-transform: skewX(45deg);
            transform: skewX(45deg);
    -webkit-transition: all 0.45 cubic-bezier(0.86, 0, 0.07, 1);
    -webkit-transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1);
    transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1); }
  .brk-btn:hover {
    box-shadow: 0 0 0px 1px rgba(255, 255, 255, 0.5), 2px 2px 6px rgba(6, 229, 222, 0.4), -2px -2px 6px rgba(170, 255, 169, 0.4); }
    .brk-btn:hover::before {
      right: 80%; }
    .brk-btn:hover::after {
      left: 80%; }

